/*     */
.stechHeader {
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 16px 24px;
    background: #141414; /*   */
    color: #e5e7eb; /*    */
}

/*     */
.headerLeft {
    display: flex;
    align-items: center;
}

.topRow {
    display: flex;
    flex-direction: row;
}

.bottomRow {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    margin-left: 24px;
    border-bottom: 1px solid #fff;
}

.teamLogo {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 8px;
}

.teamPickerWrap {
    position: relative;
}

.teamPicker {
    display: flex;
    align-items: center;
    gap: 4px;
    background: transparent;
    border: none;
    color: inherit;
    font-size: 26px;
    font-weight: 600;
    cursor: pointer;
    padding-left: 6px;
}

/* : absolute      */
.teamDropdown {
    position: absolute;
    top: calc(100% + 4px); /*    */
    left: 0;
    background: #1c1d21;
    border: 1px solid #303136;
    border-radius: 6px;
    padding: 6px 0;
    width: 180px;
    z-index: 100;
    list-style: none;
}

.teamItem {
    display: flex;
    align-items: center;
    gap: 8px;
    width: 100%;
    padding: 6px 12px;
    background: transparent;
    border: none;
    color: #e5e7eb;
    cursor: pointer;
}
.teamItem:hover {
    background: #2a2b30;
}

/*      */
.filterGroup {
    display: flex;
    gap: 8px;
}

.filterButton,
.resetButton {
    font-size: 14px;
    padding: 6px 12px;
    border-radius: 6px;
    border: 1px solid #303136;
    background: #1c1d21;
    color: #e5e7eb;
    display: flex;
    align-items: center;
    gap: 4px;
    cursor: pointer;
}

.filterButton:hover,
.resetButton:hover {
    background: #2a2b30;
}

.resetButton {
    border-color: #3b82f6;
    background: #3b82f620; /*    */
    color: #3b82f6;
}

/*  NewVideo   */
.newVideoButton {
    padding: 8px 20px;
    font-size: 14px;
    font-weight: 600;
    color: #fff;
    background: linear-gradient(135deg, #4f46e5 0%, #3b82f6 100%);
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: transform 0.15s, box-shadow 0.15s;
    box-shadow: 0 4px 12px rgba(59, 130, 246, 0.35);
}

.newVideoButton:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 16px rgba(59, 130, 246, 0.45);
}

.newVideoButton:active {
    transform: translateY(0);
}

/* Date picker :   */
.datePickerWrap {
    position: relative;
}

/*  date input */
.dateDropdown {
    position: absolute;
    top: calc(100% + 4px); /*    */
    left: 0;
    background: #1c1d21;
    color: #e5e7eb;
    border: 1px solid #303136;
    border-radius: 6px;
    padding: 6px 8px;
    z-index: 100;
}

/*     width  */
.dateDropdown::-webkit-calendar-picker-indicator {
    cursor: pointer;
}

.filterButton.active,
.typeItem.active,
.calCell.selected {
    background: linear-gradient(135deg, #4f46e5 0%, #3b82f6 100%);
    color: #fff;
}

/*  TYPE   */
.typePickerWrap {
    position: relative;
}
/*   */
.typeDropdown {
    position: absolute;
    top: calc(100% + 4px); /*    */
    left: 0;
    background: #1c1d21;
    border: 1px solid #303136;
    border-radius: 6px;
    padding: 4px 0;
    width: 180px;
    z-index: 120; /*   */
    list-style: none;
}

.typeItem {
    width: 100%;
    padding: 6px 12px;
    background: transparent;
    border: none;
    color: #e5e7eb;
    cursor: pointer;
}

.typeItem:hover {
    background: #2a2b30;
}
.typeItem.active {
    background: linear-gradient(135deg, #4f46e5 0%, #3b82f6 100%);
    color: #fff;
}

/*  OPPS   */
.oppsPickerWrap {
    position: relative;
}
.oppsDropdown {
    position: absolute;
    top: calc(100% + 4px);
    left: 0;
    width: 220px;
    background: #1c1d21;
    border: 1px solid #303136;
    border-radius: 6px;
    padding: 4px 0;
    z-index: 120;
    list-style: none;
}
.oppsHeader {
    font-weight: 700;
    color: #fff;
    padding: 8px 12px;
    border-bottom: 1px solid #303136;
}
.oppsItem {
    display: flex;
    align-items: center;
    gap: 8px;
    width: 100%;
    padding: 6px 12px;
    background: transparent;
    border: none;
    color: #e5e7eb;
    cursor: pointer;
}
.oppsItem:hover {
    background: #2a2b30;
}

/*    */
.oppsItem img,
.teamItem img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
}

/*  TYPE    */
.typeItem {
    width: 100%;
    padding: 6px 12px;
    background: transparent;
    border: none;
    color: #e5e7eb;
    cursor: pointer;
}

/*       */
@media (max-width: 640px) {
    .filterGroup {
        display: none; /*   () */
    }
}
